export default {
	name: 'uni-badge',
	props: {
		type: {
			type: String,
			default: 'default'
		},
		inverted: {
			type: Boolean,
			default: false
		},
		text: {
			type: String,
			default: ''
		},
		size: { //small.normal
			type: String,
			default: 'normal'
		}
	},
	computed: {
		setClass() {
			let classList = ['uni-badge-' + this.type, 'uni-badge--' + this.size];
			if (this.inverted === true) {
				classList.push('uni-badge-inverted')
			}
			return classList.join(" ")
		}
	},
	methods: {
		onClick() {
			this.$emit('click')
		}
	}
}
